    startTracking = async () => {
        this.setState({ isLoading: true });
		const { workoutStore } = this.props.stores;
        const { workoutid } = this.props;
        const { currentTeam, newWorkout = {} } = workoutStore;
        if (workoutid) {
            await workoutStore.load(workoutid);
            const updateObject = {
                isInProgress: 1,
            };
            await workoutStore.save(updateObject, workoutid);
        } else {
            const workouts = await workoutStore.load({ query: { team: currentTeam }, skipUpdate: 1, limit: 5, sort: '-createdDate' });
            let lastWorkout = workouts[0];
            const lastWoutoutObject = {};
            if (lastWorkout) {
                lastWoutoutObject.dogs = lastWorkout.dogs || [];
                lastWoutoutObject.dogsLeft = [];
                lastWoutoutObject.dogsRight = [];
                lastWoutoutObject.mushers = lastWorkout.mushers;
                lastWoutoutObject.type = lastWorkout.type || 2;
                lastWoutoutObject.equipment = lastWorkout.equipment;
                lastWoutoutObject.sledWeight = lastWorkout.sledWeight;
            }

            const response = await workoutStore.insert({
                name: `New workout tracking ${util.isoDate()}`,
                team: currentTeam,
                isInProgress: 1,
                ...lastWoutoutObject,
                type: newWorkout.type || 1,
            });

            await workoutStore.cordovaResetTracking();
            await workoutStore.load(response.data.id);
        }

        const { workout } = workoutStore;
        workoutStore.cordovaStartTracking({ workout: workout.id, team: currentTeam });
        // this.setTimers();
        this.setState({ isLoading: false });
    }

