    resumeTracking = async () => {
		const { workoutStore } = this.props.stores;
        await workoutStore.cordovaResumeTracking();
        const { workout, currentTeam } = workoutStore;
        const updateObject = {
            isInProgress: 1,
            team: currentTeam,
        };
        await workoutStore.save(updateObject, workout.id);
    }

    pauseTracking = async () => {
		const { workoutStore } = this.props.stores;
        await workoutStore.cordovaPauseTracking();
        const { workout } = workoutStore;
        const updateObject = {
            isInProgress: 2,
        };
        await workoutStore.save(updateObject, workout.id);
    }

    finishTracking = async () => {
        this.setState({ isSaving: true });
		const { workoutStore } = this.props.stores;
        await workoutStore.cordovaSaveTracking();
        await workoutStore.cordovaResetTracking();
        const { workout, currentTeam } = workoutStore;
        const updateObject = {
            isInProgress: 0,
            team: currentTeam,
        };
        await workoutStore.save(updateObject, workout.id);
        this.setState({ isSaving: false });

        route('/workouts/list');
    }

